:root {
  --font-Inter: "Inter", sans-serif;
  --font-Rubik: "Rubik", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(to right, #fdc468, #df4996);
  --youtube: hsl(348, 97%, 39%);
  --darktoggle: linear-gradient(to right, #378fe6, #3eda82);
  --lighttoggle: hsl(230, 22%, 74%);
  --verydarkblue-bg: hsl(230, 17%, 14%);
  --verydarkblue-topbg: hsl(232, 19%, 15%);
  --card-background: hsl(228, 28%, 20%);
  --darktheme-text: hsl(228, 34%, 66%);
  --white-bg: hsl(0, 0%, 100%);
  --verypaleblue-top-bg: hsl(225, 100%, 98%);
  --light-card-background: hsl(227, 47%, 96%);
  --light-card-backgroundhover: hsl(227, 30%, 88%);
  --lighttheme-text: hsl(228, 12%, 44%);
  --lighttheme-darktext: hsl(230, 17%, 14%);
  --headercolor: hsl(0, 0%, 0%);
}

:root .dark-theme {
  --white-bg: hsl(230, 17%, 14%);
  --verypaleblue-top-bg: hsl(232, 19%, 15%);
  --light-card-background: hsl(228, 28%, 20%);
  --light-card-backgroundhover: hsl(227, 30%, 88%);
  --headercolor: hsl(0, 0%, 100%);
  --light-card-backgroundhover: hsl(227, 28%, 31%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.875rem;
  font-family: var(--font-Inter);
  background-color: var(--white-bg);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  color: var(--headercolor);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  background-color: var(--verypaleblue-top-bg);
}
.header__container {
  padding-top: 2.5rem;
  padding-bottom: 5.625rem;
}
@media (min-width: 71.875em) {
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0;
  }
}
.header__container--text {
  padding-bottom: 20px;
  border-bottom: 1px solid grey;
}
@media (min-width: 71.875em) {
  .header__container--text {
    border: none;
  }
}
.header__container--text p {
  padding-top: 5px;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--lighttheme-text);
}
.header__container--text h1 {
  margin-bottom: 0;
}
.header__container--toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__container--toggle {
    gap: 20px;
    padding: 0;
  }
}
.header__container--toggle p {
  padding-top: 5px;
  font-size: 13px;
  font-weight: 700;
  color: var(--lighttheme-text);
}
@media (min-width: 71.875em) {
  .header__container--toggle p {
    font-size: 0.8125rem;
  }
}

.themeSwitch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 26px;
}

input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: var(--lighttoggle);
  transition: 400ms;
  border-radius: 30px;
}

.slider::before {
  content: "";
  position: absolute;
  height: 19px;
  width: 20px;
  left: 26px;
  bottom: 4px;
  background-color: var(--white);
  border-radius: 50%;
  transition: 400ms;
}

input:checked + .slider {
  background: var(--darktoggle);
}

input:checked + .slider::before {
  transform: translateX(-22px);
  background-color: var(--verydarkblue-topbg);
}

.cards {
  position: relative;
}
@media (min-width: 71.875em) {
  .cards::before {
    content: "";
    background-color: var(--verypaleblue-top-bg);
    border-radius: 0 0 30px 30px;
    position: absolute;
    height: 50%;
    width: 100%;
  }
}
.cards__container {
  position: relative;
  top: -4.375rem;
}
@media (min-width: 71.875em) {
  .cards__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.25rem;
    top: 0;
    padding-top: 20px;
  }
}
@media (max-width: 71.8125em) {
  .cards__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 20px;
  }
}
@media (max-width: 39.9375em) {
  .cards__container {
    display: block;
  }
}
.cards__facebook {
  border-top: 0.25rem solid var(--facebook);
}
.cards__twitter {
  border-top: 0.25rem solid var(--twitter);
}
.cards__instagram {
  position: relative;
}
.cards__instagram::before {
  content: "";
  position: absolute;
  background: var(--instagram);
  height: 0.25rem;
  width: 100%;
  top: 0;
  border-radius: 100%;
}
.cards__youtube {
  border-top: 0.25rem solid var(--youtube);
}
.cards__card {
  margin: 1.875rem 0;
  padding: 1.875rem 0;
  max-width: 100%;
  background-color: var(--light-card-background);
  border-radius: 0.3125rem;
  box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
}
@media (min-width: 71.875em) {
  .cards__card {
    margin: 0;
  }
}
.cards__card--logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}
.cards__card--logo p {
  color: var(--lighttheme-text);
  font-size: 0.6875rem;
  font-weight: 700;
}
.cards__card--text {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1.5625rem;
  gap: 0.3125rem;
}
.cards__card--text h2 {
  font-size: 2.8125rem;
  font-weight: 800;
}
.cards__card--text p {
  color: var(--lighttheme-text);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  padding-top: 0.3125rem;
}
.cards__card--indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1.5625rem;
  gap: 0.3125rem;
}
.cards__card--indicator p {
  font-size: 0.75rem;
  color: var(--limegreen);
  font-weight: 700;
}
.cards__card:hover {
  background-color: var(--light-card-backgroundhover);
  cursor: pointer;
  transform: scale(1.05);
  transition: all 400ms ease-in-out;
}

.red-down p {
  color: var(--brightred);
}

@media (min-width: 71.875em) {
  .overview__text {
    padding-top: 50px;
  }
}
.overview__text h2 {
  color: var(--lighttheme-text);
  font-size: 26px;
}
@media (min-width: 71.875em) {
  .overview__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 20px;
  }
}
@media (max-width: 71.8125em) {
  .overview__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 20px;
  }
}
@media (max-width: 39.9375em) {
  .overview__cards {
    display: block;
  }
}
.overview__card {
  margin: 1.875rem 0;
  padding: 30px 25px 20px;
  max-width: 100%;
  background-color: var(--light-card-background);
  border-radius: 0.3125rem;
  box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
}
.overview__card:hover {
  background-color: var(--light-card-backgroundhover);
  cursor: pointer;
  transform: scale(1.05);
  transition: all 400ms ease-in-out;
}
.overview__card--top {
  display: flex;
  justify-content: space-between;
}
.overview__card--top p {
  color: var(--lighttheme-text);
  font-size: 14px;
  font-weight: 700;
}
.overview__card--bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  align-items: baseline;
}
.overview__card--bottom-img {
  display: flex;
  align-items: center;
  gap: 3px;
}
.overview__card--bottom-img p {
  color: var(--limegreen);
  font-weight: 700;
}
.overview__card--bottom h3 {
  font-size: 35px;
}

.red-down p {
  color: var(--brightred);
}