:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);

  // ###Primary

  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(to right, #fdc468, #df4996);
  --youtube: hsl(348, 97%, 39%);

  // Dark Theme

  --darktoggle: linear-gradient(to right, #378fe6, #3eda82);

  // Light Theme

  --lighttoggle: hsl(230, 22%, 74%);

  // ______________________

  // ###Neutral

  // Dark Theme

  --verydarkblue-bg: hsl(230, 17%, 14%);
  --verydarkblue-topbg: hsl(232, 19%, 15%);
  --card-background: hsl(228, 28%, 20%);
  --darktheme-text: hsl(228, 34%, 66%);

  // Light Theme

  --white-bg: hsl(0, 0%, 100%);
  --verypaleblue-top-bg: hsl(225, 100%, 98%);
  --light-card-background: hsl(227, 47%, 96%);
  --light-card-backgroundhover: hsl(227, 30%, 88%);
  --lighttheme-text: hsl(228, 12%, 44%);
  --lighttheme-darktext: hsl(230, 17%, 14%);
  --headercolor: hsl(0, 0%, 0%);
}

:root .dark-theme {
  --white-bg: hsl(230, 17%, 14%);
  --verypaleblue-top-bg: hsl(232, 19%, 15%);
  --light-card-background: hsl(228, 28%, 20%);
  --light-card-backgroundhover: hsl(227, 30%, 88%);
  --headercolor: hsl(0, 0%, 100%);
  --light-card-backgroundhover: hsl(227, 28%, 31%);;
}
